import React from 'react'
import classNames from 'classnames'
import wood from './../../img/wood.jpg'
import style from './Slide.module.scss'

const innerClasses = classNames({
    inner: true,
    inner_2: true
})

const Slide_2= () => {

    return (
        <div className={innerClasses} 
            style= {{
                backgroundImage: 'url(' + `${wood}` + ')',
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat'
            }}
        >
            <section className={style.content}>
                <div className={style.portfolio}>
                    <a target="_blank" href="https://millsfield.com/" className={style.site}>millsfield.com</a>
                    <a target="_blank" href="http://tmidv.ru/" className={style.site}>Техмашинвест</a>
                    <a target="_blank" href="https://iqkomarov.ru/" className={style.site}>iqkomarov.ru</a>
                    <a target="_blank" href="https://dvizhuch.ru/" className={style.site}>Движуч</a>
                    <a target="_blank" href="http://specavtomash-m.ru/" className={style.site}>Спецавтомаш</a>
                    <a target="_blank" href="https://gefest-74.ru/" className={style.site}>Гефест</a>
                    <a target="_blank" href="https://avto-llumar.ru/" className={style.site}>Тонировка авто</a>

                </div>
           </section>
        </div>
    )
}

export default Slide_2